import React, { useState, useEffect } from 'react'

const MyData = () => {
  const [quotes, setQuotes] = useState([])

  useEffect(() => {
    getData()
  }, [])

  const getData = async () => {
    const data = await fetch('https://www.breakingbadapi.com/api/quotes')
    const quotesObject = await data.json()
    setQuotes(quotesObject)
    console.log(quotesObject)
  }

  return (
    <div>
      <section>
        {quotes.map((quote) => (
          <li key={quote.quote_id}>
            <h3>{`Quote #${quote.quote_id} by the author: ${quote.author}`}</h3>
            <h5>{`Epic quote: "${quote.quote}"`}</h5>
          </li>
        ))}
      </section>
    </div>
  )
}

export default MyData
